import * as types from "./types"
import Storage from "./storage"
import { id } from "./utils"

const date = new Date()
const today = date.toDateString()
const DB = new Storage({
  name: "yatl",
  store: [
    {
      name: "todos",
      key: "id"
    },
    {
      name: "late",
      key: "id"
    }
  ]
})

export const init = () => async dispatch => {
  await DB.init()

  const [active, late] = await Promise.all([
    DB.getAll("todos"),
    DB.getAll("late")
  ])

  const todos = [],
    newLates = [],
    ids = []

  active.forEach(todo => {
    if (todo.category === "Today" && todo.date !== today) {
      newLates.push(todo)
      ids.push(todo.id)
    } else todos.push(todo)
  })

  dispatch({
    type: types.INITIAL_LOAD,
    todos: todos.reverse(),
    late: [...newLates, ...late].reverse()
  })

  if (newLates.length !== 0) {
    Promise.all([DB.save("late", newLates), DB.delete("todos", ids)])
  }
}

export const delete_todo = (ids, store = "todos") => dispatch => {
  DB.delete(store, ids).then(() => {
    dispatch({
      type: types.REMOVE_TODO,
      store,
      ids
    })
  })
}

export const add_todo = (todos, store = "todos") => dispatch => {
  DB.save(store, todos).then(() => {
    dispatch({
      type: types.ADD_TODO,
      store,
      todos
    })
  })
}

export const create_todo = values => dispatch => {
  const todo = {
    ...values,
    id: id(),
    date: today
  }

  if (values.category === "") {
    todo.category = "Today"
  }

  dispatch(add_todo(todo))
}

export const update_todo = (updated, store = "todos") => dispatch => {
  DB.update(store, updated).then(() => {
    dispatch({
      type: types.UPDATE_TODO,
      updated,
      store
    })
  })
}

export const edit_todo = todo => dispatch => {
  dispatch({
    type: types.EDIT_TODO,
    todo
  })
}

export const to_complete = (ids, store) => (dispatch, getState) => {
  const { todos, stats } = getState()

  const day = Reflect.ownKeys(stats.weekDay)[date.getDay()]
  const ids = todos[store]
    .filter(task => ids.includes(task.id))
    .map(todo => todo.id)
  const completed = ids.length

  stats.completed += completed
  stats.weekDay[day] += completed

  Promise.all([
    dispatch(delete_todo(ids, store)),
    dispatch({
      type: types.UPDATE_STATS,
      stats
    })
  ])
}



