import React, { Component } from "react"
import { Redirect } from "react-router-dom"
import { connect } from "react-redux"

import Todos from "../components/Todos"

class Complete extends Component {
  state = {
    checked: []
  }

  deleteTodos = () => {
    this.props.deleteTodos(this.state.checked)
  }

  render() {
    const { category, tasks } = this.props

    if (this.props.loading) {
      return <Redirect to="/" />
    }

    return (
      <React.Fragment>
        {category.map(value => (
          <Todos key={value} header={value} todos={tasks[value]} />
        ))}
      </React.Fragment>
    )
  }
}

const state = ({ complete, loading }) => {
  const tasks = complete.reduce((acc, todo) => {
    let key = todo.category
    if (acc[key]) {
      acc[key].push(todo)
    } else {
      acc[key] = [todo]
    }
    return acc
  }, {})

  const category = Object.keys(tasks)

  return {
    tasks,
    category,
    loading
  }
}

export default connect(state)(Complete)



